import { Calendar } from '@fullcalendar/core';
import adaptivePlugin from '@fullcalendar/adaptive';
import interactionPlugin from '@fullcalendar/interaction';
import dayGridPlugin from '@fullcalendar/daygrid';
import listPlugin from '@fullcalendar/list';
import timeGridPlugin from '@fullcalendar/timegrid';
import resourceTimelinePlugin from '@fullcalendar/resource-timeline';
import './index.css'

const machines = require('./data/machines.json');
const allJobs = require('./data/jobs.json');
const allResources = require('./data/resources.json');
const machineIds = machines.map(machine => machine.id);
const allowedJobStatuses = ['Planned', 'Started', 'Finished'];
const allowedTaskStatuses = ['Planned', 'Started', 'Finished'];

document.addEventListener('DOMContentLoaded', function () {
    let calendarEl = document.getElementById('calendar');

    const activeJobs = allJobs.filter(job => allowedJobStatuses.includes(job.ExecuteStatus));
    const allActiveTasks = activeJobs.reduce((accumulator, job) => {
        const tasks = job.Tasks || [];
        const activeTasks = tasks.filter(task => allowedTaskStatuses.includes(task.TaskStatus));
        activeTasks.forEach(task => {
            task.job = job;
            task.resin = task.CustomFieldValue1 || 'N/A';
            task.lines = task.job.CustomFieldValue1 || 'N/A';
            task.id = task.Guid;
            task.title = task.Name;
            task.start = task.Start;
            task.end = task.End;
            const isSetup = task.SetupTime > 0 || /SET\s?UP/i.test(task.Name);
            if (task.TaskStatus === 'Planned') {
                task.color = isSetup ? 'lightcoral' : 'royalblue';
            } else if (task.TaskStatus === 'Started') {
                task.color = isSetup ? 'firebrick' : 'darkblue';
            } else if (task.TaskStatus === 'Finished') {
                task.color = isSetup ? 'olive' : 'olivedrab';
            } else {
                task.color = 'red';
            }
            const assignedResources = task.AssignedResources || [];
            const matchingResources = assignedResources.filter(resource =>
                machineIds.includes(resource.Guid)
            );
            if (matchingResources) {
                task.resourceIds = matchingResources.map(resource => resource.Guid);
            }
        });
        return accumulator.concat(activeTasks);
    }, []);

    const machineResources = allResources.filter(resource => machineIds.includes(resource.Guid));
    const exceptionEvents = machineResources.reduce((accumulator, machine) => {
        const calendarExceptions = machine.CalendarExceptions;
        const events = calendarExceptions.map(exception => {
            const exceptionDay = new Date(exception.Date);
            const nextDay = new Date(exceptionDay.getTime() + 24 * 60 * 60 * 1000);
            const event = {
                display: 'background',
                color: 'lightpink',
                start: exceptionDay,
                end: nextDay,
                resourceIds: [machine.Guid],
            }
            return event;
        });
        return accumulator.concat(events);
    }, []);

    let calendar = new Calendar(calendarEl, {
        plugins: [adaptivePlugin, interactionPlugin, dayGridPlugin, listPlugin, timeGridPlugin, resourceTimelinePlugin],
        schedulerLicenseKey: 'XXX',
        now: '2023-12-04 10:00',
        height: '100%',
        editable: true,
        aspectRatio: 1.8,
        scrollTime: '00:00',
        headerToolbar: {
            left: 'title',
            center: '',
            right: 'timelineThreeDays,timelineThreeWeeks,timelineThreeMonths today prev,next'
        },
        buttonText: {
            today: 'Today',
        },
        nowIndicator: true,
        initialView: 'timelineThreeWeeks',
        views: {
            timelineThreeDays: {
                type: 'resourceTimeline',
                buttonText: 'Day',
                duration: { days: 3 },
                slotDuration: { hours: 1 },
                slotLabelInterval: { hours: 3 },
            },
            timelineThreeWeeks: {
                type: 'resourceTimeline',
                buttonText: 'Week',
                duration: { weeks: 3 },
                slotDuration: { hours: 3 },
                slotLabelInterval: { hours: 6 },
            },
            timelineThreeMonths: {
                type: 'resourceTimeline',
                buttonText: 'Month',
                duration: { months: 3 },
                slotDuration: { hours: 8 },
                slotLabelInterval: { days: 1 },
            },
        },
        resourceAreaHeaderContent: 'Machines',
        resourceAreaWidth: '200px',
        expandRows: true,
        resourceOrder: 'order',
        resources: machines,
        events: [...allActiveTasks, ...exceptionEvents],
        eventClick: function (info) {
            if (info.event.title === '') return;
            const messageData = {
                'Task: ': info.event.title,
                'Resin: ': info.event.extendedProps.resin,
                'Lines: ': info.event.extendedProps.lines,
            };
            const combinedString = Object.entries(messageData).reduce((accumulator, [key, value]) => {
                return accumulator + `${key}: ${value}\n`
            }, '');
            alert(combinedString);
        }
    });

    calendar.render();
});
